<template>
    <div class="l-model">
        <img class="l-logo" src="../assets/logo.png" alt="" />
        <h1 class="l-m-title"></h1>
        <form action="" id="registerForm">
            <div class="rowView">
                <span class="l-subtitle"></span>
                <input v-bind:class="showaccount?'l-tipsBorder':''" class="l-inputbox" @focus="hidetip" type="text" name="registerEmail" v-model="account" />
            </div>
            <div class="rowView">
                <span class="l-subtitle"></span>
                <input v-bind:class="showpassword?'l-tipsBorder':''" class="l-inputbox" @focus="hidetip" type="password" placeholder="6-20" maxlength="20" v-model="password" />
            </div>
            <div class="rowView">
                <span class="l-subtitle"></span>
                <input v-bind:class="showpasswordAgain?'l-tipsBorder':''" class="l-inputbox" type="password" @focus="hidetip" maxlength="20" v-model="passwordAgain" />
            </div>
            <div class="rowView">
                <div class="l-tips">{{tips}}</div>
            </div>
            <div class="rowView" style="margin: 0;">
                <div class="l-read">
                    <input type="checkbox" @change="hidetip" name="" v-model="read" /><router-link to=""></router-link><router-link to=""></router-link>
                </div>
            </div>
            <input class="l-submitBtn" type="button" value="" @click="register"/>
            <div class="rowView" style="margin-top: 10px;margin-bottom: 0;">
                <div class="l-backlogin"> <router-link to="/"></router-link></div>
            </div>
        </form>
        <h2 class="l-support">95</h2>
        <div v-show="mask" class="l-mask">
            <div class="l-mask-model">
                <div class="l-mask-modelChild">
                    <h3></h3>
                    <p>{{masktext}}</p>
                    <div class="l-btnBox">
                        <button @click="hidemask" class="yes"></button>
                        <router-link style="margin-left:35px" v-show="backlogin" to="/"><button></button></router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            mask:false,
            masktext:"",
            showaccount:false,
            showpassword:false,
            showpasswordAgain:false,
            tips:"",
            backlogin:false,
            account:"",
            password:"",
            passwordAgain:"",
            read:true
        }
    },
    methods:{
        register:function(){
            if (!this.account) {
                this.showaccount = true;
                this.tips = ""
            } else if(!this.password) {
                this.showpassword = true;
                this.tips = ""
            } else if(this.password.length<6) {
                this.showpassword = true;
                this.tips = ""
            } else if(!this.passwordAgain) {
                this.showpasswordAgain = true;
                this.tips = ""
            } else if(!(this.password == this.passwordAgain)) {
                this.showpasswordAgain = true;
                this.tips = ""
            } else if(!this.read) {
                this.tips = ""
            } else {
                var that = this;
                axios.post('http://139.196.73.183:8088/register', {
                    userName:this.account,
                    userPwd:this.password
                })
                .then(function (response) {
                    console.log(response)
                    if (response.data.errorCode=='0000') {
                        that.account = "",
                        that.password = "",
                        that.passwordAgain = "",
                        that.mask = true,
                        that.masktext = "!",
                        that.backlogin = true
                    }else{
                        that.mask = true
                        that.masktext = response.data.errorMsg
                    }
                })
                .catch(function (error) {
                    console.log(error);
                }); 
            }
        },
        hidetip:function(){
            this.showaccount = false;
            this.showpassword = false;
            this.showpasswordAgain = false;
            this.tips = ""
        },
        hidemask:function(){
            this.mask = false
        },
    }
}
</script>



