 <template>
  <div class="l-bg">
    <div class="l-container">
      <div class="l-srction-tips"></div>
      <div class="l-resetInfo">
        <input
          class="l-formSubmitBtn l-resetSubmitBtn"
          type="button"
          @click="resetData"
          value=""
        >
        <input type="button" class="l-formSubmitBtn l-resetSubmitBtn" @click="resetShare" value="" >        
      </div>
      <div class="l-srction-tips"></div>
      <div class="l-resetSearch">
        <input
          class="l-formMiddleSmallerInput"
          @click="showCouponStartTime"
          readonly="readonly"
          v-model="couponStartTime"
        >
        <input
          class="l-formMiddleSmallerInput"
          @click="showCouponEndTime"
          readonly="readonly"
          v-model="couponEndTime"
        >
        <date-time ref="couponStartTime" type="date" @confirm="setCouponStartTime"></date-time>
        <date-time ref="couponEndTime" type="date" @confirm="setCouponEndTime"></date-time>
        <input class="l-searchButton" type="button" @click="couponSearch" value="">
      </div>
      <div class="l-resetInfo">
        <table border="1" class="table">
          <tr class="tableTitle">
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="item in count" :key="item.shopId">
            <td>{{item.shopId}}</td>  
            <td>{{item.shopName}}</td>
            <td>{{item.couponState==0?"":""}}</td>
            <td>{{item.couponCount}}</td>
          </tr>
          <tr v-if="noCount">
            <td colspan="4"></td>
          </tr>
        </table>
      </div>
      <div class="l-srction-tips"></div>
      <div class="l-resetSearch">
        <input
          class="l-formMiddleSmallerInput"
          @click="showClickStartTime"
          readonly="readonly"
          v-model="clickStartTime"
        >
        <input
          class="l-formMiddleSmallerInput"
          @click="showClickEndTime"
          readonly="readonly"
          v-model="clickEndTime"
        >
        <date-time ref="clickStartTime" type="date" @confirm="setClickStartTime"></date-time>
        <date-time ref="clickEndTime" type="date" @confirm="setClickEndTime"></date-time>
        <input class="l-searchButton" type="button" @click="clickSearch" value="">
      </div>
      <div class="l-resetInfo">
        <table border="1" class="table">
          <tr class="tableTitle">
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="item in clickCount" :key="item.shopId">
            <td>{{item.shopId}}</td>
            <td>{{item.shopName}}</td>
            <td>{{item.scanTimes}}</td>
            <td>{{item.buttonCount}}</td>
            <td>{{item.shareCount}}</td>
          </tr>
          <tr v-if="noClickCount">
            <td colspan="5"></td>
          </tr>
        </table>
      </div>
      <div class="l-resetInfo">
        <div>
          <p class="l-mark" v-for="(item,index) in markList" :key="index">{{item}}</p>
        </div>
      </div>
    </div>
    <!---->
    <div v-if="mask" class="l-mask">
      <div class="l-mask-model">
        <div class="l-mask-modelChild">
          <h3></h3>
          <p>{{masktext}}</p>
          <div class="l-btnBox">
            <button class="yes" @click="hidemask"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DateTime from "vue-date-time-m";
import axios from "axios";
export default {
  data() {
    return {
      mask: false,
      masktext: "",
      couponStartTime: this.getNowFormatDate(),
      couponEndTime: this.getNowFormatDate(),
      clickStartTime: this.getNowFormatDate(),
      clickEndTime: this.getNowFormatDate(),
      count: "",
      noCount: false,
      clickCount: "",
      noClickCount: false,
      markList:["","1","211"]
    };
  },
  components: {
    DateTime
  },
  methods: {
    // 
    showmask: function(masktext) {
      this.mask = true;
      this.masktext = masktext;
    },
    // 
    hidemask: function() {
      this.mask = false;
    },
    // 
    resetData: function() {
      var that = this;
      axios
        .get("http://139.196.73.183:8088/pointrecord/resetstatus")
        .then(function(response) {
          // console.log(response);
          if (response.data.errorCode == "0000") {
            that.showmask("");
          } else {
            that.showmask(response.data.errorMsg);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //
    resetShare:function(){
      var that = this;
      axios.get("https://www.95cfun.cn/custShare/updShareStatue?shareStyle=05").then(function(response){
        if(response.data.errorCode == "0000"){
          // console.log(response);
          that.showmask("");
        }else{
          that.showmask(response.data.errorCode);
        }
      }).catch(function(error){
        console.log(error);
      });
    },
    // 
    //  
    showCouponStartTime() {
      this.$refs.couponStartTime.show();
    },
    // 
    setCouponStartTime(val) {
      this.couponStartTime = val.replace(new RegExp("/", "g"), "-");
    },
    // 
    showCouponEndTime() {
      this.$refs.couponEndTime.show();
    },
    setCouponEndTime(val) {
      this.couponEndTime = val.replace(new RegExp("/", "g"), "-");
    },
    // 
    showClickStartTime() {
      this.$refs.clickStartTime.show();
    },
    setClickStartTime(val) {
      this.clickStartTime = val.replace(new RegExp("/", "g"), "-");
    },
    // 
    showClickEndTime() {
      this.$refs.clickEndTime.show();
    },
    setClickEndTime(val) {
      this.clickEndTime = val.replace(new RegExp("/", "g"), "-");
    },
    //
    couponSearch: function() {
      var that = this;
      axios
        .get(
          "http://139.196.73.183:8088/statistics?startTime=" +
            this.couponStartTime +
            "&endTime=" +
            this.couponEndTime
        )
        .then(function(response) {
          // console.log(response);
          if (response.data.data.length > 0) {
            that.count = response.data.data;
          } else {
            that.noCount = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //
    clickSearch: function() {
      var that = this;
      axios
        .get(
          "http://139.196.73.183:8088/statistics/clickcount?startTime=" +
            this.clickStartTime +
            "&endTime=" +
            this.clickEndTime
        )
        .then(function(response) {
          // console.log(response);
          if (response.data.data.length > 0) {
            that.clickCount = response.data.data;
          } else {
            that.noClickCount = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  mounted: function() {
    //
    this.couponSearch();
    //
    this.clickSearch();
  }
};
</script>
<style>
.date-time-item {
  width: 500px;
  height: 300px;
  background: #eeeeee;
}
.date-time-input {
  width: 100px;
  height: 40px;
  background: pink;
  margin: 5px;
}
.l-mark{
  margin-bottom: 8px;
}
</style>




